<template>
  <div class="todo-info">
    <span class="total">{{total}} item left</span>
    <div class="tabs">
      <a 
      v-for="(item, index) in states"
      :key="index"
      :class="['btn','primary','border',state===item ? 'actived' : '']"
      @click='toggleState(item)'>
      {{item}}
      </a>
    </div>
    <button class="btn info" @click="delCompleted">Clear Completed</button>
  </div>
  
</template>

<script>
  export default {
    name: 'TodoInfo',
    data() {
      return {
        states: ['all', 'active', 'completed'],
        state: 'all'
      }
    },
    props: {
      total: Number,
    },
    methods: {
      delCompleted() {
        this.$emit('del')
      },
      toggleState(item) {
        this.state = item   
        this.$emit('toggle', item)
      },
    }
  }
</script>

<style lang="stylus" scoped>
  @import '~@/assets/styles/theme.styl'
  @import '~@/assets/styles/mixins.styl'

  .todo-info 
    display: flex
    justify-content: space-between
    font-weight 400

    
    .total
      color: $red

    .tabs {
      display: flex;
      justify-content: space-between;
      width: 200px;
    }

    .btn.primary.border
      primaryBorderBtn()
      &.actived
        primaryBtn()
    
    .btn.info
      infoBtn()

     
  
</style>