<template>
  <div class="main-todo">
    <input type="text" class="add-todo" placeholder="what to do?" autofocus v-model='content' @keyup.enter='addTodo'>
    <todo-item v-for='(item, index) in filterData' :key='index' :todo='item' @del='handleDeleteItem'></todo-item>
    <todo-info :total="total" @del='del' @toggle='toggle'></todo-info>
  </div>
</template>

<script>
  import TodoItem from '@/components/MainTodo/coms/TodoItem.vue'
  import TodoInfo from '@/components/MainTodo/coms/TodoInfo.vue'
  let id = 0
  export default {
    name: 'MainTodo',
    data() {
      return {
        todoData: JSON.parse(localStorage.getItem('todoData')) || [],
        content: '',
        total: 0,
        filter: 'all',
      }
    },
    mounted() {
      window.addEventListener('beforeunload', () => {
        localStorage.setItem('todoData', JSON.stringify(this.todoData))
      })
    },
    // destroyed在页面刷新和标签页关闭都不会触发，写成updated触发又太频繁
    // updated() {
    //   console.log(localStorage)
    //   localStorage.setItem('todoData', JSON.stringify(this.todoData))
    // },
    computed: {
        filterData() {
          switch (this.filter) {
            case 'all':
              return this.todoData
              break
            case 'active':
              return this.todoData.filter(item => item.completed == false)
              break
            case 'completed':
              return this.todoData.filter(item => item.completed == true)
              break
          }
        }
      
    },
    watch: {
      todoData: {
        deep: true,
        handler() {
          this.total = this.todoData.filter(
            item => item.completed === false
          ).length
        }
      }
    },
    methods: {
      
      addTodo() {
        if (this.content === '') return
        this.todoData.unshift({
          id: id++,
          content: this.content,
          completed: false
        })
        this.content = ''
      },

      handleDeleteItem(id) {
        this.todoData.splice(this.todoData.findIndex(item => item.id === id),1)
      },

      del() {
        this.todoData = this.todoData.filter(item => item.completed == false)
      },

      toggle(state) {
        this.filter = state
      }
    },
    components: {
      TodoItem,
      TodoInfo
    },
  }
</script>

<style lang="stylus" scoped>
  @import '~@/assets/styles/mixins.styl'

  .main-todo 
    margin: 0 auto
    width: 600px
    
    .add-todo 
      width: 100%
      box-sizing: border-box
      clearDefaultStyle()
      padding 10px    
  


</style> 