  @import '~@/assets/styles/theme.styl'
  @import '~@/assets/styles/mixins.styl'

  .todo-item
    display flex
    justify-content space-between
    background-color #fff
    border-top 1px solid rgba(0,0,0,.1)

    &:hover
      button:after
        content 'x'
        color $lightred

    &.completed
      label 
        color #d9d9d9
        text-decoration line-through

    input
      margin 5px

    label 
